.select_components_mask{
      position: fixed;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      background: transparent;
    }
    .select{
      width:325px;
      height:40px;
      position: relative;
      border:1px solid #ddd;
      background:#fff;
      color:#ddd;
    }
    .select-content{
      margin-top:12px;
      margin-left: 20px;
      cursor:default;
    }

    .select_arrow{
      width:38px;
      height:38px;
      position: absolute;
      right:1px;
      top:1px;
      background:#ddd;
    }
    .select_arrow span{
      position:absolute;
      margin:13px;
      width: 0;
      height: 0;
      border-left: 7px solid transparent;
      border-right: 7px solid transparent;
      border-top: 14px solid #999;
    }
    .select_wrap{
      margin: 0 0 0 -1px;;
      padding: 0;
      position: absolute;
      z-index: 2;
      width: 100%;
      top: 100%;
      border:1px solid #ddd;
      background:#fff;
    }
    .select_wrap li{
      height:40px;
      line-height: 40px;
      padding-left:18px;
      color:#000;
      list-style: none;
    }
    .select_wrap li.hover{
      background:#3e3e3e;
    }
    .select_wrap.hide{
      display: none;
    }


