.view {
  transition: all .3s ease;
  border:2px solid #000;
  margin-top:20px;
  padding:1em;
}

.fade-enter, .fade-leave {
  opacity: 0;
  transform: translate3d(0, -5px, 0);
}

.v-link-active {
  color: red;
}
.custom-active-class {
  color: #f60;
}

